.App {
  text-align: center;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #333;
  color: white;
}

.navbar-logo img {
  height: 40px;
}

.navbar-menu {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.navbar-menu li {
  margin: 0 10px;
}

.navbar-menu li a {
  color: white;
  text-decoration: none;
}

.navbar-menu li a:hover {
  text-decoration: underline;
}

.footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 10px 0;
  position: fixed;
  width: 100%;
  bottom: 0;
}

.main-content {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px;
}

.text-content {
  flex: 1;
  padding: 20px;
}

.image-content {
  flex: 1;
  padding: 20px;
}

.image-content img {
  max-width: 100%;
  height: auto;
}
